<template>
  <div>
    <div>
      <el-link
        :underline="false"
        type="primary"
        style="
          font-size: 16px;
          cursor: pointer;
          float: right;
          margin-bottom: 10px;
          color: ;
        "
        @click="add"
        ><i class="iconfont iconxinjian" style="padding-right: 5px"></i
        >新增</el-link
      >
    </div>

    <!-- <div style="margin-bottom:10px;">
        <span
          >已选择<span style="color: rgb(0, 161, 145); padding: 0 5px">{{
            tableSelection.length
          }}</span
          >项</span
        >
        <el-link
          class="table_link"
          type="danger"
          v-if="tableSelection.length"
          :underline="false"
          @click="handleMultiDelete"
          style="margin: -5px 10px 0px 10px"
          >批量删除</el-link
        >
      </div> -->
    <el-table
      v-loading="loading"
      ref="multipleTable"
      :data="tableData"
      size="small"
      border
      :height="height - 60"
      style="
        width: 100%;
        text-align: center;
        line-height: 18px;
        overflow-y: auto;
      "
    >
      <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
      <el-table-column
        label="规则种类"
        align="center"
        prop="name"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="唯一标识" align="center" prop="sign">
      </el-table-column>
      <el-table-column label="积分" align="center" width="300" prop="points">
      </el-table-column>

      <el-table-column label="操作" align="center" width="140">
        <template slot-scope="{ row }">
          <i
            class="el-icon-edit"
            style="margin-right: 20px"
            @click="handleEdit(row)"
          ></i>
          <i
            class="el-icon-delete"
            style="margin-right: 0"
            @click="handleDelete(row)"
          ></i>
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>

    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogFormVisible"
      width="650px"
      :close-on-click-modal="false"
      :append-to-body="true"
      @closed="resetForm"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm" size="mini">
        <el-form-item label="种类名称:" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="唯一标识:" prop="sign">
          <el-input v-model="form.sign" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="积分:" prop="points">
          <el-input-number
            v-model="form.points"
            :min="-10"
            :max="10"
          ></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm" size="mini">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')" size="mini"
          >提 交</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "rule_cop",
  components: {},
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loading: false,
      tableData: [
        {
          name: "荐书",
          sign: "aixk",
          points: "2",
        },
      ],
      dialogFormVisible: false,
      dialogTitle: "新增",
      form: {
        name: "",
        sign: "",
        points: "",
      },
      rules: {
        name: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        sign: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32个字符",
            trigger: "blur",
          },
        ],
        points: [{ required: true, message: "请输入", trigger: "blur" }],
      },
    };
  },
  created() {},
  mounted() {},
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.form);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    resetForm() {
      this.$refs["ruleForm"].resetFields();
      this.form = {
        name: "",
        sign: "",
        points: "",
      };
      this.id = null;
      //this.getTableData();
    },

    add() {
      this.dialogTitle = "新增";
      this.dialogFormVisible = true;
    },
    handleEdit(row) {
      this.dialogTitle = "编辑";
      let data = JSON.parse(JSON.stringify(row));
      this.form = data;
      this.dialogFormVisible = true;
      this.id = data.id;
    },

    handleDelete(row) {
      this.$confirm("确定删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        closeOnClickModal: false,
      }).then(() => {
        console.log(row);
      });
    },
  },
};
</script>
<style scoped></style>
